.home-container {
    font-family: Arial, sans-serif;
    text-align: center;
    padding: 20px;
    background-color: #f4f4f4;
}

.home-container h1 {
    color: #333;
    margin-bottom: 20px;
}

nav {
    margin: 20px 0;
}

nav a {
    text-decoration: none;
    color: #007bff;
    margin: 0 15px;
    font-size: 18px;
}

nav a:hover {
    text-decoration: underline;
}
