.manage-polls-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    background-color: #fff;
}

.manage-polls-container h2 {
    text-align: center;
    color: #333;
    margin-bottom: 20px;
}

.manage-polls-container h3 {
    margin-top: 20px;
    color: #444;
}

.manage-polls-container ul {
    list-style: none;
    padding: 0;
}

.manage-polls-container li {
    border-bottom: 1px solid #ddd;
    padding: 10px 0;
}

.manage-polls-container li:last-child {
    border-bottom: none;
}

.manage-polls-container strong {
    display: block;
    margin-bottom: 5px;
    color: #333;
}

.manage-polls-container button {
    padding: 10px 15px;
    border: none;
    border-radius: 4px;
    background-color: #007bff;
    color: #fff;
    font-size: 14px;
    cursor: pointer;
    transition: background-color 0.3s;
    margin-right: 10px;
    margin-top: 5px;
}

.manage-polls-container button:hover {
    background-color: #0056b3;
}

.manage-polls-container form {
    margin-top: 20px;
}

.manage-polls-container label {
    display: block;
    margin-bottom: 10px;
    font-size: 16px;
    color: #555;
}

.manage-polls-container input {
    width: calc(100% - 22px);
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 14px;
    margin-bottom: 10px;
}

.manage-polls-container .error-message {
    color: red;
    text-align: center;
    margin-bottom: 15px;
}
