.admin-panel-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    background-color: #fff;
}

.admin-panel-container h2 {
    text-align: center;
    color: #333;
    margin-bottom: 20px;
}

.admin-panel-container nav {
    display: flex;
    justify-content: center;
    gap: 15px;
}

.admin-panel-container a {
    text-decoration: none;
    color: #007bff;
    font-size: 16px;
    font-weight: bold;
    transition: color 0.3s;
}

.admin-panel-container a:hover {
    color: #0056b3;
}
